.plans-container {
  margin-top: -4rem;
  padding: 0 2rem;
  display: flex;
  flex-direction: column;
  gap: 4rem;
  position: relative;
}

.plans {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}
.plans-blur1 {
  width: 32rem;
  height: 23rem;
  top: 6rem;
  left: 0;
}
.plans-blur2 {
  width: 32rem;
  height: 23rem;
  top: 10rem;
  right: 0;
}
.plan {
  display: flex;
  flex-direction: column;
  background-color: var(--caloryCard);
  color: white;
  gap: 2rem;
  padding: 1.5rem;
  width: 15rem;
}
.plans > :nth-child(2) {
  background: var(--planCard);
  transform: scale(1.1);
}
.plan > :nth-child(1) {
  font-size: 1rem;
  font-weight: bold;
}
.plan > :nth-child(2) {
  font-size: 3rem;
  font-weight: bold;
}
.plan > :nth-child(4) {
  font-size: 0.8rem;
}
.features {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.feature {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.feature > img {
  width: 1rem;
}
.plans > :nth-child(2) > button {
  color: var(--orange);
}
@media screen and (max-width: 1080px) {
  .plans {
    flex-direction: column;
  }
  .plans > :nth-child(2) {
    transform: none;
  }
}
